:root {
  --red: #fe4443;
  --grey: #f0efef;
  --grey2: #6e6e6e;
  --lightBlack: #464955;
  --black: #13222a;
}

* {
  margin: 0;
  padding: 0;
}

body {
  box-sizing: border-box;
  overscroll-behavior: none;
  scrollbar-width: thin;
  scrollbar-color: var(--red);
  background-color: orange;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  overflow-x: hidden;
}

::-webkit-scrollbar {
  width: 3px;
}

::-webkit-scrollbar-thumb {
  background-color: var(--red);
  border-radius: 2px;
}

#html_canvas {
  width: 100%;
  height: 100%;
}

#canvas-container {
  pointer-events: none;
  z-index: -1;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
}

.loader {
  z-index: 10;
  background-color: var(--grey);
  transform: transform 1s;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2em;
  width: 100%;
  height: 100vh;
  font-size: 12px;
  display: none;
  position: absolute;
}

.progress {
  background-color: var(--red);
  z-index: 10;
  transform-origin: 0;
  width: 50%;
  height: 1px;
  margin: 0 5%;
  position: absolute;
  top: 60%;
  transform: scaleX(0);
}

main {
  scroll-snap-points-y: repeat(100vh);
  height: 100vh;
  overflow: hidden scroll;
}

section {
  color: orange;
  scroll-snap-align: center;
  width: 100vw;
  height: 100vh;
  font-size: 42px;
}

