:root{
    --red: #fe4443;
    --grey: #f0efef;
    --grey2: #6e6e6e;
    --lightBlack: #464955;
    --black: #13222a;
}

*{
    margin: 0;
    padding: 0;
}
body{
    font-family: sans-serif;
    padding: 0;
    background-color: orange;
    margin: 0;
    box-sizing: border-box;

    overflow-x: hidden;
    overscroll-behavior: none;

    scrollbar-width: thin;
    scrollbar-color: var(--red);
}

::-webkit-scrollbar{
    width: 3px;
}

::-webkit-scrollbar-thumb{
    background-color:  var(--red);
    border-radius: 2px;
}
#html_canvas{
    width: 100%;
    height: 100%;

}
#canvas-container{
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    pointer-events: none;
    z-index: -1;
}


.loader{
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    z-index: 10;
    background-color: var(--grey);
    gap: 2em;
    font-size: 12px;
    transform: transform 1s;
    display: none;
}

.progress{
    position: absolute;
    top: 60%;
    width: 50%;
    height: 1px;
    background-color: var(--red);
    z-index: 10;
    margin: 0 5%;
    transform: scaleX(0);
    transform-origin: left;

}
main{
    overflow-y: scroll;
    overflow-x: hidden;
    height: 100vh;
    /* z-index: 100; */
    
    /* scroll-snap-type: y mandatory; */
    scroll-snap-points-y: repeat(100vh);
}

section{
    width: 100vw;
    height: 100vh;
    color: orange;

    scroll-snap-align: center;
    font-size: 42px;
}